* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: monospace;
}

body {
  color: #777;
  font-size: 10px;
  background-color: #003eb2;
}

.App {
  display: flex;
  width: 100vw;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

.AppWorld {
  position: relative;
}

.AppWorldLayer {
  position: absolute;
  top: 0;
  left: 0;
}
